/* 6. Write CSS rules that cause the header and .collage div to be centered on the page */
/* 7. Write CSS rules that ensure that no more than 4 photos appear on a row */

html {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

body {
    max-width: 850px;
}

header, .collage {
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

header {
    top: 0;
    position: -webkit-sticky;
    position: sticky;
    text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
    color: orange;
}

img {
    margin: 5px;
}


